class ModeloTresDiferenciado extends Component {
  styleFirstSpan = {
    fontSize: '14pt',
    color: '#a01f1f'
  }

  styleResposta = {
    color: '#ff0000'
  }

  render() {
    return (
      <div class="container">
        <br /> <br />
        <div>
          <p className="paragraph-modelo3">
            <span style={this.styleFirstSpan}><strong>Pergunta 1</strong></span>&nbsp;
          </p>
          <p className='paragraph-two-modelo2'>
            Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
          </p>
          <textarea class="form-control-modelo3" rows="10" cols="70" placeholder="Digite sua resposta"></textarea>
        </div>
      	<div class="collapse-modelo3">
      		<p>
      			<span style={this.styleResposta}>Resposta:<br /><br />
      				Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.
      			</span>
      		</p>
      	</div>
        <div class="btn-modelo3">
          Mostrar Gabarito
        </div>
      </div>
    )
  }
}

